<template>
  <Form :name="name"
        :ref="name"
        :id="name"
        :autocomplete="autocomplete"
        @submit="onSubmit"
        @reset="onReset"
  >
    <slot :validateAll="validateAll"
          :validateField="validateField">
    </slot>
  </Form>
</template>

<script>
import { Form } from 'vee-validate';

export default {
  name: 'ui-form',
  components: {
    Form,
  },
  props: {
    /** form name */
    name: String,
    /** form autocomplete */
    autocomplete: String
  },
  data() {
    return {
      /** form field   */
      formOldValues: null,
    }
  },
  mounted() {
    const form = document.getElementById(this.name);
    this.formOldValues = Object.fromEntries(new FormData(form));
  },
  methods: {
    /** form   */
    onSubmit(values) {
      this.$emit('onsubmit', values);
    },
    /** form reset  */
    onReset() {
      /**
       * 2023.01.14.
       * vee-validate form reset ,      reset    
       *   -    form    .
       */
      for (let i = 0; i < Object.keys(this.formOldValues).length; i++) {
        const key = Object.keys(this.formOldValues)[i];

        this.$refs[this.name].setFieldValue(key, this.formOldValues[key]);
      }

      this.$emit('onreset');
    },
    /**  Field    */
    validateAll() {
      return this.$refs[this.name].validate();
    },
    /** 1 Field    */
    validateField(field) {
      return this.$refs[this.name].validateField(field);
    },
  },
}
</script>

